cking context\n#root, #__next {\n    isolation: isolate;\n}\n\n// Remove all animations, transitions and smooth scroll for people that prefer not to see them\n@media (prefers-reduced-motion: reduce) {\n    html:focus-within {\n        scroll-behavior: auto;\n    }\n\n    *, *::before, *::after {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}\n","// Credit for implementing SCSS themes: https://medium.com/@dmitriy.borodiy/easy-color-theming-with-scss-bc38fd5734d1\n@import 'themes';\n\n:root {\n    $active-theme: $dark-theme;\n\n    @each $property, $value in $active-theme {\n        --#{$property}: #{$value};\n    }\n  }\n  \nhtml[data-theme='light'] {\n    @each $property, $value in $light-theme {\n        --#{$property}: #{$value};\n    }\n}\n\nhtml[data-theme='dark'] {\n    @each $property, $value in $dark-theme {\n        --#{$property}: #{$value};\n    }\n}\n","// Content\n\n.title-editable:hover,\n.title-editable:focus {\n    background-color: var(--modal-option-hover);\n}\n\n#content {\n    grid-area: content;\n    padding: 0 24px 24px 24px;\n    overflow: hidden;   // Required for task scrolling on overflow\n    display: grid;\n    grid-template-rows: max-content 1fr max-content;\n    gap: 16px;\n\n    > * {\n        width: 100%;\n        user-select: none;\n    }\n}\n\n#project-title {\n    height: max-content;\n    // font-family: \"Paytone One\", sans-serif;\n    color: var(--content-title-text);\n    transition: $default-transition;\n\n    > * {\n        width: max-content;\n        border-radius: 8px;\n        padding: 4px 12px;\n    }\n    &-1 {\n        font-size: 40px;\n    }\n    &-2 {\n        margin-top: -12px;\n        font-size: 20px;\n    }\n}\n\n#project-display-scroll {\n    overflow-y: hidden; // Required for task scrolling on overflow\n\n    #project-display {\n        max-height: 100%; // Required for task scrolling on overflow\n        overflow-y: auto; // Required for task scrolling on overflow\n        \n        #active-tasks,\n        #completed-tasks {\n            display: grid;\n            grid-auto-rows: max-content;\n            gap: 4px;\n        }\n\n        #completed-tasks-title {\n            display: none;\n            height: max-content;\n            margin-top: 24px;\n            margin-bottom: 8px;\n            padding: 4px 12px;\n            font-size: 24px;\n            // font-family: \"Paytone One\", sans-serif;\n            color: var(--content-title-text);\n            transition: $default-transition;\n        }\n    }\n    #project-display::-webkit-scrollbar {\n        width: 8px;\n    }\n    #project-display::-webkit-scrollbar-thumb {\n        background-color: var(--content-title-text);\n    }\n}\n\n#add-task {\n    height: 60px;\n    padding: 8px;\n    border-radius: 8px;\n    background-color: $background;\n    box-shadow: $background-shadow;\n\n    display: grid;\n    grid-template-columns: 44px 1fr;\n    gap: 8px;\n\n    > * {\n        height: 100%;\n        background-color: transparent;\n    }\n    span {\n        font-size: 32px;\n        font-weight: 300;\n        color: grey;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n    }\n    &-input {\n        font-size: 20px;\n        font-weight: 400;\n        color: var(--content-body-text);\n    }\n}\n\n#add-task:hover {\n    background-color: $background-hover;\n}\n\n.task {\n    padding: 8px;\n    border-radius: 8px;\n    background-color: $background;\n    user-select: text;\n\n    display: grid;\n    grid-template-columns: 44px 1fr;\n    grid-template-areas:\n        \"checkbox description\"\n        \"checkbox info\";\n    column-gap: 8px;\n\n    > *:not(:first-child) {  // Allow pointer events on checkbox only\n        pointer-events: none;\n    }\n    .task-checkbox {\n        grid-area: checkbox;\n        justify-self: c