nsition;\n        cursor: pointer;\n\n        display: flex;\n        align-items: center;\n        justify-content: center;\n    }\n    #nav-close:hover { \n        background-color: var(--modal-option-hover);\n    }\n}\n\n.nav-links {\n    max-height: calc(100% - $nav-header-height - $nav-add-project-height - 4px);\n    display: grid;\n    grid-template-rows: repeat(2, max-content) 1fr;\n    gap: 4px;\n\n    > * {\n        transition: $default-transition;\n    }\n    .default-projects {\n        display: grid;\n        grid-auto-rows: 60px;\n        gap: 4px;\n    }\n    hr {\n        margin: 24px 0;\n        border: 1px solid rgb(220, 220, 220);  \n    }\n    .custom-projects-scroll {\n        overflow-y: hidden;    // Required for nav scrolling on overflow\n        display: flex;\n        flex-direction: column;\n\n        .custom-projects {\n            max-height: 100%;      // Required for nav scrolling on overflow\n            overflow-y: auto;      // Required for nav scrolling on overflow\n            display: grid;\n            grid-auto-rows: 60px;\n            gap: 4px;\n    \n            > * {\n                transition: $default-transition;\n            }\n        }\n    }\n    .custom-projects::-webkit-scrollbar {\n        width: 8px;\n    }\n    .custom-projects::-webkit-scrollbar-thumb {\n        background-color: var(--content-title-text);\n    }\n}\n\n.nav-item {\n    padding-left: 32px;\n    padding-right: 16px;\n    border-radius: 8px;\n\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    > * {\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n        gap: 16px;\n        pointer-events: none;\n    }\n    .project-task-count {\n        height: 32px;\n        width: 32px;\n\n        border-radius: 50%;\n        color: white;\n        background-color: var(--body-bg-gradient-top);\n        font-size: 1.17em;\n        font-weight: 400;\n\n        display: flex;\n  